body{
    background-image:url("./img/FondoTrello.png") ;
    background-size: cover; /* La imagen de fondo se reescala autom√°ticamente con el cambio del ancho de ventana del navegador */
}

.encabezado{
    margin-top: 45px;
    background-color: rgb(58, 103, 114);
    border:1px solid rgb(45, 80, 88);
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center; 
    height: 70px;
    font-size: 1.3rem;
} 
.textoEncabezado{
    margin-top: 8px;
    text-shadow: rgba(0, 0, 0, 0.315) 4px 2px 3px ;
} 

.App{
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
}

.lista{
    margin-top: 20px;
    background-color:rgb(91, 139, 151);
    width: 300px;
    height:auto;
    padding: 7px 10px 17px;
    border:1px solid rgb(69, 106, 116);
    border-radius: 5%;
    display:flex;
    box-shadow:10px 4px 5px rgba(0, 0, 0, 0.315);
}

.tituloLista{
    margin:5px;
    color: white;
    text-shadow: rgba(0, 0, 0, 0.315) 4px 2px 3px ;
}

.tarea{
    background-color: rgba(255, 255, 255, 0.459);
    height: auto;
    width: 270px;
    padding: 8px;
    margin:10px 5px;
    border-radius: 7px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.2rem ;
    display: flex;
    flex-direction:row ;
    justify-content: space-between;   
}

.Xbtn{
    background-color: rgba(255, 255, 255, 0.246);
    height: 25px;
    width: 25px;
    border-radius: 50%;
    margin: 2px;
    padding: 2px;
    text-align: center;
    cursor: pointer; 
}
.Xbtn:hover{
    background-color: rgba(20, 20, 20, 0.103);
    color:white;
}


.XbtnLista{
    background-color: rgba(199, 137, 118, 0.972);
    color:white;
    border:1px solid rgba(199, 137, 118, 0.972);
    margin-top:10px ;
    margin-left:5px ;
    width: 270px;
    height: 30px;
    padding:7px;
    text-align: center;
    border-radius: 5px;
    box-shadow:4px 2px 3px rgba(0, 0, 0, 0.315);
    cursor: pointer; 
    font-weight: bold;
}
.XbtnLista:hover{
    background-color: rgb(248, 160, 138);
    color:rgba(221, 60, 11, 0.972);
   
}


