.containetInputCard{
    background-color:rgba(138, 192, 194, 0.568);
    border: 1px solid rgba(73, 99, 100, 0.513);
    margin-top: 10px;
    margin-left: 4px ;
    width: 270px;
    height:auto;
    padding: 7px 10px 8px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border-radius: 5px;
    box-shadow:5px 4px 5px rgba(0, 0, 0, 0.315);
}


.inputCard{
    margin-top:15px ;
    margin-left:5px ;
    width: auto;
    height: auto;
}

.btnAdd{
    background-color: rgba(78, 168, 180, 0.808);
    color:rgb(44, 44, 44);
    margin-top:20px ;
    margin-left:5px ;
    width: 270px;
    height: 30px;
    padding:7px;
    text-align: center;
    border-radius: 5px;
    box-shadow:4px 2px 3px rgba(0, 0, 0, 0.315);
    cursor: pointer;
    font-weight: bold;
}
 .btnAdd:hover{
    background-color: rgba(44, 75, 77, 0.719);
    color:white;
} 

